<template>
  <q-dialog ref="dialog" @hide="onDialogHide" maximized>
    <q-card class="q-dialog-plugin">
      <q-card-section class="row items-center q-pb-none">
        <div class="text-h6"></div>
        <q-space />
        <q-btn icon="close" flat round dense v-close-popup />
      </q-card-section>
      <q-separator />
      <q-card-section style="max-height: 100%">
        <sign-canvas
          class="sign-canvas sign-image"
          ref="SignCanvas"
          :options="options"
      /></q-card-section>
      <q-card-actions align="right">
        <q-btn color="primary" label="" @click="onOKClick" />
        <q-btn color="primary" label="" @click="onCancelClick" />
      </q-card-actions>
    </q-card>
  </q-dialog>
</template>

<script >
import SignCanvas from "sign-canvas";
export default {
  name: "contact",
  components: {
    SignCanvas,
  },
  data() {
    return {
      options: {
        isDpr: false, //dpr [Boolean] 
        lastWriteSpeed: 1, // [Number] 
        lastWriteWidth: 2, // [Number] 
        lineCap: "round", // [butt] [round] [square]	
        lineJoin: "bevel", //  [bevel] [round] [miter]
        canvasWidth: 320, //  [Number] 
        canvasHeight: 200, //  [Number] 
        isShowBorder: false, // []
        bgColor: "#F2F2F2", // [String] 
        borderWidth: 1, //   [Number] 
        borderColor: "black", //  [String] 
        writeWidth: 5, //  [Number] 
        maxWriteWidth: 30, //   [Number] 
        minWriteWidth: 5, //   [Number] 
        writeColor: "#101010", //   [String] 
        isSign: true, // [Boolean] ,, true
        imgType: "png", //  [String]  jpeg  canvas
      },
    };
  },
  methods: {
    show() {
      this.$refs.dialog.show();
    },
    hide() {
      this.$refs.dialog.hide();
    },
    onDialogHide() {
      this.$emit("hide");
    },
    onOKClick() {
      const res = this.$refs.SignCanvas.saveAsImg();
      this.$emit("ok", res);
      this.hide();
    },
    onCancelClick() {
      this.hide();
    },
  },
};
</script>

